
.rd-btn{position:relative;display:inline-block;text-align:center;vertical-align:middle;touch-action:manipulation;cursor:pointer;border:1px solid transparent;white-space:nowrap;font-size:.14rem;height:.33rem;min-width:.66rem;border-radius:4px;padding:0 .06rem;-webkit-transition:background 0.2s;transition:background 0.2s;outline:none;border-color:#d9d9d9
}
.rd-btn i{margin:0 .05rem
}
.rd-btn.default:after, .primary:after, .rd-btn-icon:after{background-color:#000;content:" ";opacity:0;top:0;right:0;bottom:0;left:0;position:absolute;border-radius:4px
}
.rd-btn.default:active:after, .primary:active:after, .rd-btn-icon:active:after{opacity:0.3
}
.rd-btn.default{color:#333;background-color:#fff;border-color:#d9d9d9
}
.rd-btn.primary{color:#fff;background-color:#57c5f7;border:none
}
.rd-btn.rd-btn-icon{width:.33rem;min-width:initial;border-radius:50%;padding:0
}
.rd-btn.rd-btn-icon i{margin:0
}
.rd-btn.rd-btn-icon:after{border-radius:50%
}
.rd-btn-icon-only{vertical-align:baseline
}
.rd-btn.noborder{border:none;background:none;min-width:auto
}
.rd-btn.noborder:active{color:#57c5f7
}
.rd-btn.rd-btn-small{height:.24rem;min-width:.44rem
}
.rd-btn.rd-btn-large{font-size:.16rem;height:.4rem;min-width:.8rem
}
.rd-btn.rd-btn-block{display:block;width:100%;height:.4rem
}
.rd-btn.rd-btn-icon.rd-btn-small{width:0.3rem
}
.rd-btn.rd-btn-icon.rd-btn-large{width:.4rem
}
.rd-btn.rd-btn-disabled{color:#c3c3c3;background:#d9d9d9;cursor:not-allowed
}
.rd-btn.rd-btn-disabled:active{color:#c3c3c3;border-color:#d9d9d9
}
.rd-btn.rd-btn-disabled:active:after{opacity:0
}



