
.lm-input-row{display:-webkit-box;display:flex;position:relative;height:.4rem;font-size:.16rem;text-align:center;border:1px solid rgba(0,0,0,0.2);border-radius:3px;background-color:#fff;color:#000
}
.lm-input-row input, .lm-input-row pre, .lm-input-row span{height:100%;line-height:.38rem
}
.lm-input-row .list{position:absolute;top:100%;width:100%;max-height:3rem;z-index:100;background-color:#fff;margin-top:2px;border:1px solid #D9D9D9;border-top:none
}
.lm-input-row .list li{border-bottom:1px solid #D9D9D9;padding:0.1rem 0;margin-left:.1rem
}
.lm-input-row .lm-input-box{-webkit-box-flex:1;flex:1;position:relative;padding-left:.1rem
}
.lm-input-row .lm-input-box input{padding-right:.2rem;height:100%;width:100%;outline:0;-webkit-appearance:none;background:none;border:none
}
.lm-input-row .lm-input-box span{position:absolute;width:.2rem;right:0;top:0;height:100%
}
.lm-input-row .left{padding-left:0.05rem
}
.lm-input-row .right-icon{height:100%;line-height:.38rem;float:right;text-align:center;width:.4rem;font-size:.18rem
}
.lm-input-row .right{text-align:center;width:.56rem;font-size:.12rem;border-left:1px solid #D9D9D9
}
.lm-input-row .right:active{color:#57C5F7
}



