
.lm-check-icon{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;-webkit-box-pack:center;justify-content:center
}
.lm-check-icon{width:0.2rem;height:0.2rem;border:1px solid #ccc;border-radius:50%;color:red;font-size:12px
}
.lm-check-icon:before{opacity:0
}
.lm-check-icon.checked:before{opacity:1
}
.lm-check-icon.disabled{background:#f3f3f3
}



