
.lm-button{position:relative;overflow:hidden;border:none;border-radius:4px;height:0.4rem;box-shadow:0 2px 5px 0 rgba(0,0,0,0.26);height:.33rem;padding-left:0.12rem;padding-right:0.12rem;font-size:0.14rem
}
.lm-button.lm-btn-block{display:block;width:100%;height:.4rem;font-size:0.16rem
}
.lm-button.disabled{color:#c3c3c3;background:#d9d9d9;cursor:not-allowed
}
.ripple-effect{position:absolute;border-radius:50%;width:20px;height:20px;background:white;-webkit-animation:ripple 1.5s;animation:ripple 1.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards
}
@-webkit-keyframes ripple{
from{-webkit-transform:scale(1);transform:scale(1);opacity:0.4
}
to{-webkit-transform:scale(50);transform:scale(50);opacity:0
}
}
@keyframes ripple{
from{-webkit-transform:scale(1);transform:scale(1);opacity:0.4
}
to{-webkit-transform:scale(50);transform:scale(50);opacity:0
}
}



