
#going .driverInfo .call{background:none;border:none;border-radius:0;box-shadow:none
}
#going .content .cell-right>*:last-child{float:right
}
#going .content, #going .pay{margin-left:auto;margin-right:auto
}
#going .content .line{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;margin:0 auto
}
#going .content .line:before, #going .content .line:after{display:block;-webkit-box-flex:1;flex:1;content:"";height:1px;border-top:1px solid #999
}
#going .content .line:before{margin-right:10px
}
#going .content .line:after{margin-left:10px
}
#going .driverInfo .call{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;-webkit-box-pack:center;justify-content:center
}
#going .driverInfo{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;padding:0.1rem
}
#going .driverInfo>.text-group{align-self:flex-start
}
#going .driverInfo>*:nth-child(2){display:block;-webkit-box-flex:1;flex:1
}
#going .driverInfo img{width:60px;height:60px;border-radius:50%
}
#going .driverInfo .call{width:50px;height:50px;border-radius:50%;background-color:#FFF
}
#going .driverInfo .text-group{padding-left:5px;font-size:0.14rem
}
#going .content{width:80%
}
#going .content .line{color:#999;margin:0.2rem auto
}
#going .content .cell-right{margin:0 auto 0.1rem
}
#going .pay{width:90%;color:#fff;margin-top:30px;background-color:red
}



